body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

html, body {
  background-image: -webkit-linear-gradient(lightblue, green);
  background-image: linear-gradient(lightblue, green);
  height: 100%;
  /* display: flex; */
}

.App {
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: space-around;
          justify-content: space-around;
  
}

.list {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
 
  -webkit-align-items: center;
 
          align-items: center;
  margin-top: 2rem;
  
}

button {
  background-color: rgb(180, 155, 180);
  border: 1px solid rgb(0, 1, 8);
  box-shadow: 2px 2px; 
  width: 5rem;
  height: 3rem;
  /* margin-left: 3rem; */
  margin-top: 2rem;
  font-size: 2rem;

}

#listItem {
  width: 20rem;
  height: 3rem;
  font-size: 1.5rem;
  background-color: lightgrey;
}

li {
  background-image: -webkit-linear-gradient(lightblue, grey);
  background-image: linear-gradient(lightblue, grey);
  border: 1px solid white;
  margin-bottom: 1rem;
  padding: 0 1rem;

}

.toDoList {
  list-style-type: none;
font-size: 2rem;
font-family: Impact, Haettenschweiler, 'Arial Narrow Bold', sans-serif;
text-shadow: 2px;
}

.completedList {
  list-style-type: none;
  font-size: 2rem;
  font-family: 'Lucida Sans', 'Lucida Sans Regular', 'Lucida Grande', 'Lucida Sans Unicode', Geneva, Verdana, sans-serif;
text-decoration: line-through;

}
